.Account_Success {
  font-family: Roboto, "sans serif";
  margin-top: 30px;
}

.Account_Success .Title {
  display: grid;
  grid-template-columns: 15% 80%;
}

.Account_Success h1 {
  font-family: Roboto;
  font-size: 24px;
  line-height: 1.33;
  color: #2e3239;
  margin-bottom: 5%;
}

.Profile_Status img {
  width: 40%;
  margin: auto;
  display: block;
}

.Profile_Status p {
  text-align: center;
  margin: 10px 35px 9px 31px;
  font-size: 15px;
  line-height: 1.07;
  color: #24272c;
}

.Btn {
  margin-top: 10% !important;
  border-radius: 5px !important;
}
.Icons .Icon_Link {
  width: 60%;
}

.Icons_Grid {
  margin: 10px auto;
  width: 74%;
  height: 35px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Icons_Grid img {
  width: 60%;
}
